import React from 'react';
import AuthWrapper from "../components/auth-wrapper/auth-wrapper";
import {authApi} from "../services/auth/auth-service";
import {Redirect, useHistory} from "react-router-dom";
import {useAppSelector} from "../hooks/redux";
import {FORGOT_PATH} from "../utils/constants";

const ResetPassword = () => {
    const [reset, {data, isLoading, error}] = authApi.useResetPasswordMutation()
    const {prevPath} = useAppSelector(state => state.userReducer)

    const history = useHistory();

    React.useEffect(() => {
        if (data?.success) {
            history.replace({pathname: '/login'});
        }
    }, [history, data?.success])


    if (prevPath !== FORGOT_PATH) {
        return (
            <Redirect
                to={FORGOT_PATH}
            />
        );
    }

    return (
        <AuthWrapper
            title=" "
            inputs={[
                {placeholder: "  ", icon: "ShowIcon", name: "password", type: "password"},
                {placeholder: "   ", name: "token", type: "text"}
            ]}
            button={{ title: "", callback: reset, isLoading: isLoading, error: error }}
            actions={[
                { title: " ?", link: "", path: "/login" }
            ]}
        />
    );
};

export default ResetPassword;